
.ce_image_outer {
  position: relative;
  background-attachment: fixed;
  background-color: #FFFFFF;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 50vh;

  //  TODO @media screen smartphone height: 35vh ?
}

.ce_image-inner-overlay {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(#ffffff, .3);

  .container {
    height: 100%;
  }

  .container-relative {
    position: relative;
    height: 100%;
  }

  .container-relative-inner {
    text-align: center;
    padding: 3%;
    text-shadow: 0 0 10px #FFFFFF;
  }

  .ce_image-inner-title {
    font-family: "Dancing Script";
    font-size: 4rem;
  }
}