// styles for anchor menu on detail page
#anchor-menu {
  position: sticky;
  top: 70px;
  margin-bottom: 3vh;
  z-index: 9;

  ul {
    background-color: lighten($primary, 50%);
    list-style: none;
    padding: 1vh;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 10px;
  }
}

